<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true" :is-list-content="true">
    <div slot="content" class="authorize-list-page">
      <div class="slds-table--header-fixed_container authorize-list-content">
        <div class="slds-scrollable_y scroll-content">
          <table id="authorize-list" class="slds-table slds-table_bordered slds-table--header-fixed">
            <thead>
              <tr class="slds-line-height_reset">
                <th class="slds-cell-wrap first-header-label" style="width: 10rem;" scope="col">
                  <div class="slds-truncate slds-cell-fixed" title="WeChat Account">WeChat Account</div>
                </th>
                <th class="slds-cell-wrap" scope="col">
                  <div class="slds-truncate slds-cell-fixed" title="Authorize Endpoint URL">Authorize Endpoint URL</div>
                </th>
                <th class="slds-cell-wrap" scope="col" style="width: 3rem;">
                </th>
              </tr>
            </thead>
            <tbody v-if="weChatWebOAuthSettings.length > 0">
              <tr class="slds-hint-parent" v-for="(settings, index) in weChatWebOAuthSettings" :key="index">
                <th data-label="ORDER" class="first-header-label" scope="row">
                  <div class="slds-truncate" :title="settings.WeChatAccountBaseInfo.Name">{{ settings.WeChatAccountBaseInfo.Name }}</div>
                </th>
                <th data-label="NAME" scope="row">
                  <div class="slds-truncate">{{ settings.AuthorizeEndpointURL }}</div>
                </th>
                <td data-label="ACTION">
                  <action-menu :actions="authorizeEndpointURLActions" :item="settings" @edit="editWeChatWebOAuthSettings">
                  </action-menu>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="no-records" v-if="weChatWebOAuthSettings.length === 0">
            <div class="slds-text-body_regular">No Records</div>
          </div>
        </div>
      </div>

      <ld-modal
        ref="Modal"
        :title="'Edit Authorize Endpoint URL'"
      >
        <template #content>
          <div class="slds-p-around_medium url-content">
            <label class="slds-form-element__label">WeChat Account</label>
            <div class="account-container">{{ currentSettings.WeChatAccountBaseInfo.Name }}</div>
            <label class="slds-form-element__label"><abbr class="slds-required">* </abbr>Authorize Endpoint URL</label>
            <input class="slds-input" type="text" v-model="currentSettings.AuthorizeEndpointURL"/>
          </div>
        </template>

        <template #footer>
          <ld-button
            neutral
            @click="cancel()"
          >
            Cancel
          </ld-button>
          <ld-button
            brand
            :disabled="isSaveDisabled"
            @click="saveWeChatWebOAuthSettings()"
          >
            Save
          </ld-button>
        </template>
      </ld-modal>
    </div>
  </right-page>
</template>

<script>
import { LdModal } from 'charket-ui/dist/ld-modal.js'
import { LdButton } from 'charket-ui/dist/ld-button.js'
import { getWeChatWebOAuthSettings, saveWeChatWebOAuthSettings } from '../api/charket-settings'

export default {
  name: 'AuthorizeEndpointURL',
  components: {
    LdModal,
    LdButton
  },
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Authorize Endpoint URL'
      },
      weChatWebOAuthSettings: [],
      authorizeEndpointURLActions: ['Edit'],
      isSaving: false,
      currentSettings: {
        WeChatAccountBaseInfo: {}
      }
    }
  },
  computed: {
    isSaveDisabled () {
      return this.isSaving || !this.currentSettings.AuthorizeEndpointURL
    }
  },
  async created () {
    await this.fetchWeChatWebOAuthSettings()
  },
  methods: {
    async fetchWeChatWebOAuthSettings () {
      try {
        this.$bus.emit('show-loading')
        this.weChatWebOAuthSettings = await getWeChatWebOAuthSettings()
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    editWeChatWebOAuthSettings (item) {
      this.currentSettings = JSON.parse(JSON.stringify(item))
      this.$refs.Modal.open()
    },
    cancel () {
      this.$refs.Modal.close()
      this.currentSettings = {
        WeChatAccountBaseInfo: {}
      }
    },
    async saveWeChatWebOAuthSettings () {
      try {
        this.isSaving = true
        await saveWeChatWebOAuthSettings(JSON.stringify(this.currentSettings))
        this.$bus.emit('success', { content: 'WeChat Web OAuth Settings was saved.', type: 'success' })
        this.$refs.Modal.close()
        this.fetchWeChatWebOAuthSettings()
      } catch (error) {
        const errorMessage = this.$parseErrorMessage(error)
        this.$refs.Modal.showError(errorMessage)
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>

